import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Register from '@/components/Register'
import Search from '@/components/Search'

Vue.use(Router)

export default new Router({
  routes: [
    /*  {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },  */
    {
      path: '/', // /register',
      name: 'register',
      component: Register
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    }
  ]
})



